.feedback{
  padding: 30px 0 60px;
  &__inner{
    max-width: 863px;
    margin: 0 auto;
  }
  &__items{
    margin-top: 30px;
    display: grid;
    grid-template-columns: 100%;
    gap: 21px;
  }
}

.feedback-card{
  padding: 24px 28px;
  border: 1px solid #E6E6E6;
  border-radius: 5px;
  &__info{
    margin: 20px 0;
    display: flex;
    align-items: center;
  }
  &__rating{
    display: flex;
    align-items: center;
    margin-left: 15px;
    svg{
      margin-right: 1.53px;
    }
  }
  &__desc{
    max-width: 736px;
  }
}


@media screen and (max-width: 992px){
  .feedback{
    padding: 25px 0 27px;
    &__items{
      margin-top: 25px;
      gap: 17px;
    }
  }

  .feedback-card{
    padding: 15px 11px;
    &__info{
      margin-top: 15px;
    }
    &__desc{
      padding-right: 40px;
    }
  }
}
