.header{
  padding: 17px 0;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo{
    display: block;
    width: 92px;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
      height: auto;
    }
  }
  &__mob{
    flex-grow: 1;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-left: 57px;
  }
  &__lang{
    display: flex;
    align-items: center;
    position: relative;
    padding: 7px 9px;
    border: 1px solid #ECECEC;
    border-radius: 5px;
    &-bg{
      display: none;
    }
    &--active{
      .header__lang-hidden{
        pointer-events: unset;
        opacity: 1;
      }
    }
    &-show,
    &-item{
      display: flex;
      align-items: center;
      font-size: 16px;
      line-height: 19px;
      cursor: pointer;
    }
    &-icon{
      margin-right: 5px;
      flex-shrink: 0;
    }
    &-show{
      position: relative;
      z-index: 6;
    }
    &-hidden{
      z-index: 5;
      position: absolute;
      right: -10px;
      top: -5px;

      padding-top: 50px;
      opacity: 0;
      pointer-events: none;
      transition: all .3s;
      &:hover{
        border-color: #fff;
      }
    }
    &-items{
      display: grid;
      grid-template-columns: repeat(2,1fr);
      gap: 8px 11px;
      box-shadow: 0px 10px 50px rgba(0, 0, 0, 0.25);
      border-radius: 5px;
      padding: 12px 18px 12px;
      border: 1px solid #ECECEC;
      background: #FFFFFF;
    }
  }
  &__burger{
    display: none;
  }
}

.dropdown{
  padding: 12px 18px 12px;
  border: 1px solid #ECECEC;
  background: #FFFFFF;
  box-shadow: 0px 10px 50px rgba(0, 0, 0, 0.25);
  border-radius: 5px;
  &__wrap{
    z-index: 5;
    position: absolute;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
  }
}

.nav{
  display: flex;
  align-items: center;
  &__link{
    font-size: 16px;
    line-height: 125%;
    position: relative;
    &:not(:last-child){
      margin-right: 50px;
    }
    &-show{
      line-height: inherit;
      display: flex;
      align-items: center;
      &:hover{
        cursor: pointer;
        color: $primary;
        path{
          stroke: $primary;
        }
      }
      svg{
        margin-left: 11px;
      }
    }
    &-hidden{
      top: 0;
      left: 50%;
      transform: translateX(-50%);
      padding-top: 30px;
      min-width: 100%;
    }
    &--active{
      .nav__link-show{
        svg{
          transform: rotate(180deg);
        }
      }
      .nav__link-hidden{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }
  &__item{
    white-space: nowrap;
    font-size: 16px;
    line-height: 125%;
    display: flex;
    align-items: center;
    padding: 7px 0;
  }
}


@media screen and (min-width: 1350px){
  .header{
    &__lang{
      &-hidden{
        right: unset;
        left: -10px;
      }
    }
  }
}


@media screen and (max-width: 1100px){
  .header{
    &__mob{
      margin-left: 50px;
    }
  }

  .nav{
    &__link{
      &:not(:last-child){
        margin-right: 25px;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    padding: 8px 0;
    &__logo{
      width: 71px;
    }
    &__mob{
      z-index: 30;
      position: fixed;
      top: 71px;
      left: 0;
      width: 100%;
      height: calc(100vh - 71px);
      background: #FFFFFF;
      flex-direction: column;
      justify-content: unset;
      margin: 0;
      padding: 17px 10px 20px;
      overflow-y: auto;
      opacity: 0;
      pointer-events: none;
      transition: all .3s;
    }
    &__burger{
      width: 31px;
      height: 23px;
      display: flex;
      align-items: center;
      position: relative;
      z-index: 15;
      span{
        display: block;
        width: 100%;
        height: 2.5px;
        background: $black;
        left: 0;
        border-radius: 5px;
        transition: all .3s;
        &:nth-child(1){
          position: absolute;
          top: 0;
        }
        &:nth-child(3){
          position: absolute;
          bottom: 0;
        }
      }
    }
    &__lang{
      margin-top: 30px;
      &-hidden{
        right: 50%;
        transform: translateX(50%);
        width: 355px;
        max-width: calc(100vw - 20px);
        padding: 0;
        top: 50px;
      }
      &-items{
        grid-template-columns: repeat(5,1fr);
      }
      &--active{
        .header__lang-show{
          pointer-events: none;
        }
        &~.header__lang-bg{
          display: block;
        }
      }
      &-bg{
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100vh;
      }
    }
    &._open{
      .header{
        &__burger{
          span{
            &:nth-child(1){
              transform: rotate(-45deg) translate(-9px,5px);
            }
            &:nth-child(2){
              transform: scale(0);
            }
            &:nth-child(3){
              transform: rotate(45deg) translate(-9px,-5px);
            }
          }
        }
        &__mob{
          opacity: 1;
          pointer-events: unset;
        }
      }
    }
  }

  .nav{
    flex-direction: column;
    &__link{
      font-size: 20px;
      line-height: 200%;
      &:not(:last-child){
        margin: 0;
      }
    }
  }
}


@media screen and (max-width: 385px){
  .header{
    &__lang{
      &-items{
        grid-template-columns: repeat(4,1fr);
      }
    }
  }
}
