.main{
  background-image: url(../img/main-bg.webp);
  background-size: cover;
  background-position: center;
  padding: 40px 0 45px;
  color: #fff;
  position: relative;
  &__inner{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 10px;
  }
  &__left{
    display: flex;
    flex-direction: column;
    max-width: 583px;
    align-items: center;
  }
  &__img{
    position: relative;
    z-index: 3;
    width: 487px;
    height: auto;
    display: block;
    margin: 0 auto;
    //filter: drop-shadow(0px 0px 200px #8F0094);
  }
  &__title2{
    margin-top: 20px;
    text-align: center;
    br{
      display: none;
    }
  }
  &__title{
    width: 100%;
  }
  &__desc{
    margin-top: 20px;
    width: 100%;
  }
  &__btn{
    margin: 25px auto 0 0;
    width: 470px;
  }
  &__girl{
    position: absolute;
    top: 40px;
    height: 710px;
    width: auto;
  }
  &__girl1{
    left: 0;
  }
  &__girl2{
    right: 0;
  }
  &__logo{
    width: 212px;
    display: block;
    margin: 25px auto 0;
  }
}

.main--xy{
  background-image: url(../img/main-bg2.webp);
  .main__img{
    filter: none;
    width: 100%;
    margin-bottom: 30px;
  }
}

.main--s{
  background-image: url(../img/main-bg3.webp);
  .main{
    &__img{
      filter: none;
      width: 424px;
      border-radius: 10px;
      margin-bottom: 29px;
    }
  }
}

.what{
  padding: 50px 0 55px;
  &__inner{
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 760px;
    margin: 0 auto;
  }
  &__title{
    width: 100%;
  }
  &__desc{
    margin: 20px auto 23px auto;
    max-width: 760px;
  }
  &__btn{
    width: 470px;
    &:not(:last-child) {
      margin-bottom: 25px;
    }
  }
}

.how{
  background-color: $secondary;
  .what__desc{
    max-width: 972px;
  }
}

.honest{
  .what__desc{
    max-width: 964px;
  }
}

.where{
  .what__desc{
    max-width: 972px;
  }
}


@media screen and (max-width: 992px){
  .main{
    background-color: $secondary;
    padding: 0;
    background-image: none;
    &__inner{
      grid-template-columns: 100%;
      gap: 0;
    }
    &__left{
      padding: 25px 0 32px;
      color: $black;
      max-width: 100%;
    }
    &__right{
      order: -1;
      background-image: url(../img/main-bg.webp);
      background-size: cover;
      background-position: center;
      width: calc(100% + 20px);
      margin-left: -10px;
      padding: 5px 10px 30px;
    }
    &__img{
      max-width: calc(100% - 36px);
    }
    &__title2{
      margin-top: 3.5px;
    }
    &__btn{
      margin-top: 30px;
      width: 100%;
      padding: 0;
    }
    &__girl{
      height: 380px;
      top: 93px;
    }
    &__girl2{
      height: 355px;
      right: -20px;
      transform: rotate(-3.88deg);
    }
    &__logo{
      width: 140px;
      margin-top: 18px;
    }
  }

  .main--xy{
    .main{
      &__right{
        padding-bottom: 40px;
        background-image: url(../img/main-bg2.webp);
      }
      &__img{
        margin-bottom: 0;
      }
      &__title2{
        margin-top: -7px;
      }
    }
  }

  .main--s{
    .main{
      &__right{
        padding-top: 58px;
        padding-bottom: 43px;
        background-image: url(../img/main-bg3.webp);
      }
      &__img{
        max-width: calc(100% - 154.5px);
        margin-bottom: 24px;
      }
    }
  }

  .what{
    padding: 30px 0 38px;
    &__btn{
      width: 100%;
      padding: 0;
    }
  }
}


@media screen and (max-width: 576px){
  .main--xy{
    .main{
      &__img{
        width: 124%;
        max-width: unset;
        margin-left: -12%;
      }
    }
  }
}
