.display1{
  font-weight: 700;
  font-size: 40px;
  line-height: 121%;
}

.display2{
  font-weight: 700;
  font-size: 36px;
  line-height: 123%;
}

.display3{
  font-weight: 700;
  font-size: 24px;
  line-height: 123%;
}

.body-text{
  font-size: 16px;
  line-height: 120%;
  p,
  ul,
  img{
    line-height: inherit;
    &:not(:last-child){
      margin-bottom: 20px;
    }
  }
  img{
    display: block;
    max-width: 100%;
    height: auto;
    margin-left: auto;
    margin-right: auto;
  }
  strong{
    font-weight: 700;
  }
  ul{
    li{
      line-height: inherit;
      display: flex;
      &::before{
        content: '● ';
        display: block;
        margin-right: 4px;
      }
    }
  }
}




@media screen and (max-width: 992px){
  .display1{
    font-size: 30.2419px;
  }

  .display2{
    font-size: 24px;
  }

  .body-text{
    font-size: 14px;
    line-height: 17px;
  }
}
